import React, { Component } from 'react';
import Waypoint from 'react-waypoint';
import PropTypes from 'prop-types';
import { Card, Spinner } from 'components';
import styles from './styles.scss';

class Feed extends Component {
  constructor(props) {
    super(props);

    this.state = {
      page: 1,
    };
  }

  loadMoreItems = () => {
    this.setState({ page: this.state.page + 1 }, () => {
      this.props.fetchNews(this.state.page);
    });
    // console.log(this.state.page);
  };

  renderItems = () => this.props.news.map((article, index) => <Card article={article} />);

  render() {
    return (
      <div className={styles.feed}>
        <div className={styles.feed__inner}>
          {this.renderItems()}
          <div className={styles.feed__waypoint}>
            {!this.props.isFetching && <Waypoint onEnter={this.loadMoreItems} />}
            {!this.props.isEnd && <Spinner />}
          </div>
        </div>
      </div>
    );
  }
}

Feed.propTypes = {
  news: PropTypes.object.isRequired,
  fetchNews: PropTypes.func.isRequired,
  isEnd: PropTypes.bool.isRequired,
  isFetching: PropTypes.bool.isRequired,
};

export default Feed;



